import React ,{useState} from 'react'
import "./Contact.css";
import img from "./assets/type.jpg";
import Footer from"./Footer";

const Contact = () => {
    
const [values,setValues]=useState({firstName:"",lastName:"",email:""});

const [submitted,setSubmitted]= useState(false);

const [valid,setValid]=useState(false);



  const HandleFirstNameChange=(event)=>{
      setValues({...values, firstName:event.target.value})
  }
  const HandleLastNameChange=(event)=>{
    setValues({...values, lastName:event.target.value})
}
const HandleEmailChange=(event)=>{
    setValues({...values, email:event.target.value})
}

const handleSubmit=(event)=>{
event.preventDefault();
if(values.firstName&& values.lastName&& values.email){
    setValid(true);
}
setSubmitted(true);
}



    return (
        <>
            <div className="formcontainer">
         
        <div className="imggallery">
    <img src={img} />
        </div>
        <div className="contactbox">
          <h2>Login</h2>
          <form onSubmit={handleSubmit}>
              {submitted &&valid?<div className="success">Success!thank you</div> :null}
              <input  value={values.firstName}
               onChange={HandleFirstNameChange}className="form-field"
                placeholder="Enter firstname" name="firstname" />
                  {submitted && !values.firstName?<span>Pls enter FirstName</span> :null}           

              <input value={values.lastName} onChange={HandleLastNameChange}
               className="form-field" 
               placeholder="Enter lastname" name="lastname" />
                {submitted && !values.lastName?<span>Pls enter lastName</span> :null}  
               <input value={values.email} onChange={HandleEmailChange}
               className="form-field"
                placeholder="Enter email" name="email" />
                 {submitted && !values.email?<span>Pls enter Email</span> :null}  

             <button className="btn2" type="submit">Rigister</button>
          </form>
      </div>
          
        
           </div>
           
           <Footer />
    </>
    )
}

export default Contact;
